// @flow
import { connect } from 'react-redux';
import Snackbar from 'material-ui/Snackbar';
import {
  Step,
  Stepper,
  StepLabel,
  StepContent,
} from 'material-ui/Stepper';
import SelectField from 'material-ui/SelectField';
import Divider from 'material-ui/Divider';
import MenuItem from 'material-ui/MenuItem';
import RaisedButton from 'material-ui/RaisedButton';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import RenewIcon from 'material-ui/svg-icons/action/autorenew';
import FlatButton from 'material-ui/FlatButton';
import React, { Component, PropTypes } from 'react';
import 'velocity-animate';
import 'velocity-animate/velocity.ui';
import { VelocityTransitionGroup, VelocityComponent, velocityHelpers } from 'velocity-react';
import $ from 'jquery';
import styles from './App.css';


// import {} from '../actions.jsx';



const categoriesList = [
  {
    'id': 1,
    'category': 'Motivational',
  },
  {
    'id': 2,
    'category': 'Christianity',
  },
  {
    'id': 3,
    'category': 'Islam',
  },
  {
    'id': 4,
    'category': 'Buddha',
  },
  {
    'id': 5,
    'category': 'Kanye',
  },
  {
    'id': 6,
    'category': 'Movies',
  },
  {
    'id': 7,
    'category': 'Mijn eigen quote',
  },
];
const stylesList = [
  {
    'id': 1,
    'style': 'Space',
  },
  {
    'id': 2,
    'style': 'Hipster',
  },
  {
    'id': 3,
    'style': 'Helvetica',
  },
  {
    'id': 4,
    'style': 'Aggressive',
  },
  {
    'id': 5,
    'style': 'Minimal',
  },
];
const backgroundImagesList = [
  {
    url: 'https://images.unsplash.com/photo-1462726625343-6a2ab0b9f020?dpr=2&auto=format&fit=crop&w=1500&h=1000&q=80&cs=tinysrgb&crop=',
  },
  {
    url: 'https://images.unsplash.com/photo-1454817481404-7e84c1b73b4a?dpr=2&auto=format&fit=crop&w=1500&h=2250&q=80&cs=tinysrgb&crop=',
  },
  {
    url: 'https://images.unsplash.com/photo-1462331321792-cc44368b8894?dpr=2&auto=format&fit=crop&w=1500&h=913&q=80&cs=tinysrgb&crop=',
  },
  {
    url: 'https://images.unsplash.com/photo-1457327289196-f38b88d97147?dpr=2&auto=format&fit=crop&w=1500&h=998&q=80&cs=tinysrgb&crop=',
  },
  {
    url: 'https://images.unsplash.com/photo-1454789591675-556c287e39e2?dpr=2&auto=format&fit=crop&w=1500&h=998&q=80&cs=tinysrgb&crop=',
  },
  {
    url: 'https://images.unsplash.com/photo-1451188502541-13943edb6acb?dpr=2&auto=format&fit=crop&w=1500&h=938&q=80&cs=tinysrgb&crop=',
  },

];



class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      finished: false,
      stepIndex: 0,
      categoryValue: 1,
      styleValue: 1,
      randomImage: 0,
      width: window.innerWidth,
      height: window.innerHeight,
    };
    this.handleKeydown = this.handleKeydown.bind(this);
    this.handleChangeCategory = this.handleChangeCategory.bind(this);
    this.handleChangeStyle = this.handleChangeStyle.bind(this);
    this.updateDimensions = this.updateDimensions.bind(this);
  }

  componentWillMount() {
    this.updateDimensions();
  }

  componentDidMount() {
    document.addEventListener('keydown', this.handleKeydown, false);
    window.addEventListener('resize', this.updateDimensions);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeydown, false);
    window.removeEventListener('resize', this.updateDimensions);
  }

  shouldComponentUpdate(nextProps, nextState) {
    return !_.isEqual(this.props, nextProps) || !_.isEqual(this.state, nextState);
  }

  componentWillReceiveProps(newProps) {}

  updateDimensions() {
    this.setState({width: $(window).width(), height: $(window).height()});
  }

  handleKeydown(e) {
    if (e.key === 'Escape') {
    }
  }

  handleNext = () => {
    const {stepIndex} = this.state;
    this.setState({
      stepIndex: stepIndex + 1,
      finished: stepIndex >= 1,
    });
  }

  handlePrev = () => {
    const {stepIndex} = this.state;
    if (stepIndex > 0) {
      this.setState({stepIndex: stepIndex - 1});
    }
  }

  renderStepActions(step) {
    const {stepIndex} = this.state;

    return (
      <div style={{margin: '12px 0'}}>
        <RaisedButton
          label={stepIndex === 1 ? 'Bekijk resultaat' : 'Volgende stap'}
          disableTouchRipple={true}
          disableFocusRipple={true}
          primary={true}
          onTouchTap={this.handleNext}
          style={{marginRight: 12}}
        />
        {step > 0 && (
          <FlatButton
            label="Terug"
            disabled={stepIndex === 0}
            disableTouchRipple={true}
            disableFocusRipple={true}
            onTouchTap={this.handlePrev}
          />
        )}
      </div>
    );
  }

  handleChangeCategory(event, index, value) {
    this.setState({categoryValue: value});
  }

  handleChangeStyle(event, index, value) {
    this.setState({styleValue: value});
  }

  render() {
    const {finished, stepIndex} = this.state;

    const categoryMenuItems = categoriesList.map((category, i) => {
      return <MenuItem value={category.id} key={i} primaryText={category.category} />;
    });
    const styleMenuItems = stylesList.map((style, i) => {
      return <MenuItem value={style.id} key={i} primaryText={style.style} />;
    });



    const randomBackgroundImage = backgroundImagesList[this.state.randomImage].url;

    const scaleFactor = (window.innerWidth / 2) / 1000;

    return (
        <div>
          <div className={styles.MainBlock}>
            <VelocityComponent
              animation={{
                top: this.state.finished ? 50 : 0,
                right: this.state.finished ? 150 : 0,
              }}
              duration={250}>

              <div id='ReportArea'
                   style={{
                     transformOrigin: 'top center',
                     transform: this.state.finished ? `scale(${scaleFactor})` : 'scale(0.8)',
                     backgroundImage: this.state.finished ? `url(${randomBackgroundImage})` : '',
                     backgroundSize: 'cover',
                   }}
                   className={`${styles.ReportArea} ${(this.state.editMode) ? styles.EditModeGrid : ''}`}>
                  <div id='Report' className={styles.Report}>
                  {(this.state.finished) ?
                    <FloatingActionButton
                      onClick={() => this.setState({
                        randomImage: parseInt(
                          Math.random() * backgroundImagesList.length),
                      })
                      }
                      style={{ right: 0, position: 'absolute', margin: 25 }}>
                      <RenewIcon />
                    </FloatingActionButton> : ''
                  }
                  </div>
              </div>
            </VelocityComponent>
          </div>

        <VelocityComponent
          animation={{
            top: (this.state.finished) ? -200 : 100,
          }}
          duration={450}>
          <div style={{maxWidth: 380, margin: 'auto', position:'relative'}}>
            <h1 className={styles.CnvsLogo}>cnvs.io</h1>

            <Stepper activeStep={stepIndex} orientation="vertical">
              <Step>
                <StepLabel>Kies een categorie</StepLabel>
                <StepContent>
                  <p>Wil je een quote uit een film of juist een diepzinnig citaat?</p>
                  <SelectField
                    value={this.state.categoryValue}
                    onChange={this.handleChangeCategory}
                    maxHeight={200}>
                    {categoryMenuItems}
                  </SelectField>
                  {this.renderStepActions(0)}
                </StepContent>
              </Step>
              <Step>
                <StepLabel>Kies een stijl</StepLabel>
                <StepContent>
                  <p>Wil je een drukke stijl of juist iets minimalistisch?</p>
                  <SelectField
                    value={this.state.styleValue}
                    onChange={this.handleChangeStyle}
                    maxHeight={200}>
                    {styleMenuItems}
                  </SelectField>
                  {this.renderStepActions(1)}
                </StepContent>
              </Step>
            </Stepper>

          </div>
          </VelocityComponent>


            {(this.state.finished) ?
              <VelocityComponent
                animation={{
                  right: (this.state.finished) ? 0 : -200,
                }}
                duration={200}>
              <div className={styles.RightSidePanel}>
                <div style={{
                  padding: '25px 20px 30px 80px',
                }}>
                  <FloatingActionButton style={{ margin: 10 }}>
                    <i className="fa fa-2x fa-facebook-official"></i>
                  </FloatingActionButton>
                  <FloatingActionButton style={{ margin: 10 }}>
                    <i className="fa fa-2x fa-twitter"></i>
                  </FloatingActionButton>
                  <FloatingActionButton style={{ margin: 10 }}>
                    <i className="fa fa-2x fa-instagram"></i>
                  </FloatingActionButton>
                </div>
                <div style={{
                  height: 140,
                  margin: '0 0 0 0',
                  position: 'absolute',
                  top: 140,
                  width: '100%',
                }}>
                  <div style={{
                    cursor: 'pointer',
                    backgroundColor: '#F6F6F6',
                    border: '1px solid #E7E7E7',
                    position: 'absolute',
                    top: 30,
                    padding: '25px 18px 25px 18px',
                    left: 110,
                    fontSize: '2.2em',
                    color: '#9B9B9B',
                    borderRadius: 4,
                  }}>A2</div>
                  <div style={{
                    cursor: 'pointer',
                    backgroundColor: '#F6F6F6',
                    border: '1px solid #E7E7E7',
                    position: 'absolute',
                    margin: '4px 0 0 0',
                    padding: '12px 8px 12px 8px',
                    top: 40,
                    left: 200,
                    fontSize: '2.2em',
                    color: '#9B9B9B',
                    borderRadius: 4,
                  }}>A3</div>
                </div>

                <RaisedButton label="Bestel deze poster" primary={true} style={{
                  position: 'absolute',
                  top: 300,
                  height: 60,
                  borderRadius: 4,
                  width: 300,
                  left: 50,
                }} />

                {finished && (
                  <RaisedButton
                    style={{
                      position: 'absolute',
                      top: 390,
                      width: 300,
                      left: 50,
                    }}
                    onClick={(e) => this.setState({stepIndex: 0, finished: false})}
                    label='Nieuwe maken'
                  />
                )}

                <Divider />
              </div>
              </VelocityComponent>
            : ''}

            {finished && (
            <h1
              style={{
                position: 'absolute',
                margin: '10px 10px 10px 10px',
                right: 0,
                bottom: 0,
              }}
              className={styles.CnvsLogo}>cnvs.io</h1>
            )}
        </div>
    );
  }
}

App.propTypes = {};

function mapStateToProps(state) {
  // This function maps the Redux state to React Props.
  return {
    'geocoder': state.geocoder,
  };
}

export default connect(mapStateToProps)(App);



